import React, {Component} from 'react';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Grid from '@material-ui/core/Grid';
import MenuItem from '@material-ui/core/MenuItem';
import Paper from '@material-ui/core/Paper';
import CircularProgress from '@material-ui/core/CircularProgress';
import { ajaxGet, ajaxPost } from '../../../utils/Ajax';

class Settings extends Component{
 
    
    constructor(props) {
        super(props)

        this.state = {
            settings : {
                GINGER_URL : '', 
                GINGER_KEY : '',  
                PAYUTC_CONNECTION_UID : '', 
                PAYUTC_CONNECTION_PIN: '', 
                PAYUTC_APP_KEY: '', 
                PAYUTC_APP_URL: '', 
                SEMESTER: ''
            },
            user : {
                login : ''
            },
            autoCompleteUsers: [],
            loading: true
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleChangeSetting = this.handleChangeSetting.bind(this);
        this.loadSettings = this.loadSettings.bind(this);
        this.autoCompleteQuery = this.autoCompleteQuery.bind(this);
        this.saveSettings = this.saveSettings.bind(this);
        this.updateBadge = this.updateBadge.bind(this);
    }


    componentDidMount(){
        this.loadSettings();
    }

    updateBadge(name){
        const login = name.match(/\(.*\)/).toString()
        const query = login.substring(1,9)
        ajaxGet('core/user?login='+query).then(res => {
            const new_user = {
                login : '',
            }
            this.setState({
                settings: {
                    ...this.state.settings,
                    PAYUTC_CONNECTION_UID : res.data.badge_uid,
                    PAYUTC_CONNECTION_PIN :  res.data.badge_pin
                }
            })
            this.setState({user : new_user, autoCompleteUsers:[]})

        })

    }

    saveSettings(){
        ajaxPost('admin/settings', {settings: this.state.settings}).then(res => {
    
        })
        .catch(error=>{
            console.log(error)
        })
    }


    handleChange(event){
        this.setState({
            user: {
                ...this.state.user,
                [event.target.name]: event.target.value
            }
        })
        if (event.target.name === 'login' && event.target.value) {
            this.autoCompleteQuery(event.target.value)
        }
    }

    handleChangeSetting(event){
        this.setState({
            settings: {
                ...this.state.settings,
                [event.target.name]: event.target.value
            }
        })
    }


    // Mthode pour obtenir de Payutc des auto compltions
    // via la valeur entre
    autoCompleteQuery(query){
        ajaxGet('payutc/user/autocomplete/' + query).then(res => {
            this.setState({autoCompleteUsers: res.data.users});
        })
        .catch(error => {
            console.log(error)
        })
    }

    loadSettings(){

        ajaxGet('admin/settings', this.state.settings).then(res => {
            const new_user = {
                login : '',
                badge : res.data.settings.PAYUTC_CONNECTION_UID,
                pin : res.data.settings.PAYUTC_CONNECTION_PIN
            }
            this.setState({settings:res.data.settings, user : new_user, loading: false}) 
        })
        .catch(res => {
            console.log(res)  
        })
 
    }
 

    render(){
        
        const { classes } = this.props;

        const {settings, autoCompleteUsers, user, loading} = this.state;

        if (loading) {
            return (
                <Grid 
                    container 
                    className="admin_loader"
                    direction="row"
                    justify="center"
                    alignItems="center"
                >
                    <Grid item>
                        <CircularProgress/>
                    </Grid>
                </Grid>
            )
        }

        return (
            <div className="admin_container">
                <Grid container className={classes.section}>
                    <Typography variant="h6" className={classes.subTitle}>
                        <ChevronRightIcon className={classes.subTitleIcon}/>
                        Badge PayUTC
                    </Typography>
                    <Grid container className={classes.note}>
                        Ce badge permet de dfinir avec quels identifiants la connexion  PayUTC est ralise. <br/><br/>
                        Bien que cette mthode de connexion  PayUTC pour le serveur aie une configuration contraignante (il faut, pour se connecter, l'identifiant du badge et le PIN,  la faon d'une connexion sur les caisses), c'est celle qui une fois installe permet le fonctionnement le plus rapide du logiciel.
                    </Grid>
                    <Grid container>
                        <Grid item xs={12}>
                            <TextField
                                label="Chercher un utilisateur ..."
                                className={classes.inputField}
                                name="login"
                                value={user.login|| ''}
                                onChange={this.handleChange}
                                autoComplete="off"
                                margin="dense"
                                variant="outlined"
                                fullWidth
                            />
                            <Paper className={classes.suggestions}>
                                {autoCompleteUsers.map((suggestion, index)=> (
                                    <MenuItem
                                        className={classes.suggestionItem}
                                        key={index}
                                        component="div"
                                        onClick={()=>this.updateBadge(suggestion.name.split('-')[0])}
                                        
                                    >
                                        {suggestion.name.split('-')[0]}
                                    </MenuItem>
                                ))}     
                            </Paper>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="Badge de connexion (UID)"
                                className={classes.inputField}
                                name="PAYUTC_CONNECTION_UID"
                                value={settings.PAYUTC_CONNECTION_UID|| ''}
                                onChange={this.handleChangeSetting}
                                autoComplete="off"
                                margin="dense"
                                variant="outlined"
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="Code PIN de l'utilisateur"
                                className={classes.inputField}
                                name="PAYUTC_CONNECTION_PIN"
                                value={settings.PAYUTC_CONNECTION_PIN|| ''}
                                onChange={this.handleChangeSetting}
                                autoComplete="off"
                                margin="dense"
                                variant="outlined"
                                type="password"
                                fullWidth
                            />
                            
                        </Grid>
                    </Grid>
                    
                </Grid>

                <Grid container className={classes.section}>
                    <Typography variant="h6" className={classes.subTitle}>
                        <ChevronRightIcon className={classes.subTitleIcon}/>
                        API Ginger
                    </Typography>
                    <Grid container className={classes.note}>
                        Ces paramtres permettent de dfinir d'o les informations sur les utilisateurs sont rcupres.
                    </Grid>
                    <Grid container>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="URL de l'API"
                                className={classes.inputField}
                                name="GINGER_URL"
                                value={settings.GINGER_URL}
                                onChange={this.handleChangeSetting}
                                autoComplete="off"
                                margin="dense"
                                fullWidth
                                variant="outlined"
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="Cl de connexion"
                                className={classes.inputField}
                                name="GINGER_KEY"
                                value={settings.GINGER_KEY}
                                onChange={this.handleChangeSetting}
                                autoComplete="off"
                                margin="dense"
                                variant="outlined"
                                fullWidth
                            />
                        </Grid>              
                    </Grid>
                </Grid>

                <Grid container className={classes.section}>
                    <Typography variant="h6" className={classes.subTitle}>
                        <ChevronRightIcon className={classes.subTitleIcon}/>
                        API PayUTC
                    </Typography>
                    <Grid container className={classes.note}>
                        Ces paramtres permettent de dfinir la connexion au serveur PayUTC.
                    </Grid>
                    <Grid container>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="URL de l'API"
                                className={classes.inputField}
                                name="PAYUTC_APP_URL"
                                value={settings.PAYUTC_APP_URL}
                                onChange={this.handleChangeSetting}
                                autoComplete="off"
                                margin="dense"
                                variant="outlined"
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="Cl de connexion"
                                className={classes.inputField}
                                name="PAYUTC_APP_KEY"
                                value={settings.PAYUTC_APP_KEY}
                                onChange={this.handleChangeSetting}
                                autoComplete="off"
                                margin="dense"
                                variant="outlined"
                                fullWidth
                            />
                        </Grid>              
                    </Grid>
                </Grid>

                <Grid 
                    container 
                    direction="row"
                    justify="center"
                    alignItems="center"
                >
                    <Button variant="contained" color="primary" className={classes.addButton} onClick={this.saveSettings}> 
                        Sauvegarder
                    </Button>
                </Grid>
                
            </div>
        );
    };

}

const styles = theme => ({
    container: {
        padding: 20,
        margin: 30,
        marginTop: 100,
        border: "2px solid #B22132",
    },
    section:{
        paddingBottom :30,
    },
    paper: {
        padding: 10
    },
    note: {
        backgroundColor: 'rgba(0,0,0, 0.05)',
        padding: 10,
        border: 'thin solid grey',
        marginTop: 16,
        marginBottom: 8,
    },
    inputField: {
        marginTop: 16,
        paddingLeft: 5,
        paddingRight: 5,
    },
    suggestions: {
        zIndex: 100,
        position: 'absolute',
        maxHeight: 200,
        overflowY: 'scroll',
        marginRight: 15,
    },
    suggestionItem: {
        paddingLeft: 15,
        paddingBottom: 0,
        paddingTop: 0,
        fontSize: 14,
        minHeight: 30,
    },
    addButton: {
        marginTop: 16,
        marginBottom: 8,
    },
    subTitle: {
        marginTop: 10,
        marginBottom: 10,
    },
    subTitleIcon: {
        marginRight: 8,
        paddingTop: 5,
    },
    row: {
        height: 40,
    },
    cell: {
        paddingTop: 10,
        paddingBottom: 10,
    },
    btn: {
        marginLeft: 5,
        marginRight: 5,
        marginTop: 3,
    },
});

export default withStyles (styles) (Settings)


