import React from 'react'
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import { withStyles } from '@material-ui/core/styles';


import Footer from '../components/Footer';
import WebSocketManager from '../components/websocket'
import { transitions, positions, Provider as AlertProvider } from 'react-alert'
import AlertTemplate from 'react-alert-template-basic'


const options = {
    position: positions.MIDDLE,
    timeout: 2000,
    offset: '30px',
    // you can also just use 'scale'
    transition: transitions.SCALE
  }




class Badge extends React.Component {
    constructor(props) {
        super(props);
		this.state = {
			show : false,
			infos : null
		}
	}

	hideComponent(){
    	console.log('cc')
    	this.setState({show: !this.state.show})
	}

	htmlDecode(input){
		var e = document.createElement('div');
		e.innerHTML = input;
		return e.childNodes.length === 0 ? "" : e.childNodes[0].nodeValue;
	}

	render() {

        const { classes} = this.props;
        var content = "";
		if (this.state.show){
			content =
				<div>
					<button onClick={() => this.hideComponent()}>Cacher les politiques d'utilisation des donnes</button>
					<h1>Politique d'utilisation des donnes</h1>
					<h2>Responsables</h2>
					<p>L'quipe informatique du Pic'Asso met en place cette collecte de donne pour l'administration et la mdecine de l'UTC.</p>
					<h2>Finalits du traitement</h2>
					<p>Dispositif de prvention sanitaire dans le cadre de la crise sanitaire de la covid 19.</p>
					<h2>Personnes vises par cette collecte</h2>
					<p>Tout membre de l'UTC (tudiants, professeurs, salaris) entrant dans le Pic'asso.</p>
					<h2>Les donnes stockes</h2>
					<p>Les donnes sont : date et heure du scan, login tudiant, nom et prnom de l'tudiant.</p>
					<h2>Destinataires et utilisation des donnes</h2>
					<ul>
					<li>Mdecine : contacter les potentiels cas contacts en cas d'une contamination  la COVID 19 avre.</li>
					<li>Administration : connaitre le nombre d'entres (anonymement).</li>
					</ul>
					<p>Les donnes seront communiques par l'quipe du Pic'Asso aux destinataires</p>
					<h2>Transferts</h2>
					<p>Les donnes ne feront pas objet d'un transfert dans un pays tiers ou une organisation internationale.</p>
					<h2>Conservation des donnes</h2>
					<p>Les donnes seront conserves 21 jours.</p>
				</div>
		}
		else{
			content =
			<div>
				<button onClick={() => this.hideComponent()}>Voir les politiques d'utilisation des donnes</button>
			</div>
		}
		return (
            <AlertProvider template={AlertTemplate} {...options}>

			<React.Fragment>

				<CssBaseline />

				<Container id="home" className={classes.root}>

					<Container className={classes.header} id="header" fixed>
                        <div styles={{padding:"50px"}}>
                            <h1 >Veuillez dposer votre (ancien) badge</h1>
							<p>Attendez de voir Bienvenue (Votre nom) pour vous aventurer dans le pic</p>
							<h2>Merci!!</h2>
							{content}
                        </div>
					</Container>

                    <WebSocketManager/>



				</Container>
				<Container id="footer" direction="row">
					<Footer/>
				</Container>
			</React.Fragment>
            </AlertProvider>
		);
	}
}

const styles = theme => ({
	root: {
		width:'100%',
		maxWidth: '100%',
		margin: 0,
		paddingRight: '5%',
		paddingLeft: '5%',
		paddingBottom: 50,
		backgroundColor: '#000223',
		color: 'white',
	},
	header : {
        height : window.innerHeight+65,
        textAlign: "center",
		//display: "inline-block"
	},
	title: {
		color: 'white',
		textAlign: "center",
		marginTop: 50,
	},
	container: {
		padding: 0,
		overflowX: "overlay",
	},
	component: {
		marginTop: 40,
		padding: '5%',
		border: "#B22132 1.5px solid",
		textAlign: 'left',
	},
	legend : {
		fontSize: 34,
		padding: 15,
	},
});

export default withStyles(styles)(Badge)
