import React, {Component} from 'react'
import TextField from "@material-ui/core/TextField"
import Button from "@material-ui/core/Button"
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { ajaxGet, ajaxPost } from '../utils/Ajax'
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import CircularProgress from '@material-ui/core/CircularProgress';
import { formateToDjangoDate } from '../utils/Date';
import SnackbarComponent from '../utils/SnackbarComponent';

class Charte extends Component {

  	constructor(props) {
		super(props)
		this.state = {
			newSignature : {
				nom: '',
				creneau_id: {},
				date: new Date(),
				login: '',
			},
			currentCreneau: {},
			loading: true,
			snackbar: {
				open: false,
				variant: 'success',
				message: '',
			},
			saving: false,
		}
		this.handleChange = this.handleChange.bind(this);
		this.saveSignature = this.saveSignature.bind(this);
		this.handleSnackbarClose = this.handleSnackbarClose.bind(this);
		this.changeSnackbarState = this.changeSnackbarState.bind(this);
	}


	componentDidMount(){
		this.loadCurrentPerm()
	}

	loadCurrentPerm(){
		ajaxGet('perms/current/public/creneau').then(res => {
			this.setState({
				newSignature : {
					...this.state.newSignature,
					creneau_id: res.data.id,
					date: formateToDjangoDate(new Date())
				},
				currentCreneau: res.data,
				loading: false,
			})
		})
		.catch(error => {
			this.setState({
				loading: false,
			})
			this.changeSnackbarState(true, 'error', 'Erreur lors du chargement.');
		})
	}

	displayDate() {
		let date = new Date()
		return(
			<Typography variant="body1">
				Fait le {("0" + (date.getDate())).slice(-2)}/{("0" + (date.getMonth() + 1)).slice(-2)}/{date.getFullYear()}  Compigne
				<br/><br/>
			</Typography>
		)
	}

	handleChange(event) {
		this.setState({
            newSignature: {
                ...this.state.newSignature,
                [event.target.name]: event.target.value
            }
        })
	}

	saveSignature() {
		const new_signature = this.state.newSignature;
		if (new_signature.nom && new_signature.login) {
			this.setState({saving: true})
			ajaxPost('signatures/', new_signature).then(res => {
				const current_creneau = this.state.currentCreneau;
				this.setState({
					newSignature: {
						nom: '',
						login: '',
						creneau_id: current_creneau.id,
						date: formateToDjangoDate(new Date()),
					},
					saving: false,
				})
				this.changeSnackbarState(true, 'success', 'Signature de la charte enregistre.');
			})
			.catch(error => {
				this.setState({
					saving: false,
				})
				this.changeSnackbarState(true, 'error', 'Une erreur est survenue.');
			})
		} else {
			this.changeSnackbarState(true, 'error', 'Il manque des informations.');
		}
	}


	handleSnackbarClose(){
		this.setState({
			snackbar: {
                ...this.state.snackbar,
            	open: false
			}
		})
	}

	changeSnackbarState(open, variant, message){
		this.setState({
			snackbar: {
				open: open,
				variant: variant,
				message: message
			}
		})
	}

  	render() {
		  const { currentCreneau, newSignature, loading, snackbar, saving } = this.state
		const {classes} = this.props
		return(
			<React.Fragment>
				{loading ? (
					<Grid
						container
						className={classes.loader}
						direction="row"
						justify="center"
						alignItems="center"
					>
						<Grid item>
							<CircularProgress className={classes.progress} />
						</Grid>
					</Grid>
				):(
					<div className="basic_container">
						<Typography variant="h4" className={classes.title}>
							Charte du permanencier
						</Typography>
						{currentCreneau.perm?(
							<React.Fragment>
								<Typography variant="body1" className={classes.text}>
									Ce document est valable pour toute la dure du semestre en cours et sera susceptible dtre utilis comme justificatif de
									responsabilit de ltudiant en cas de dgradation ou de comportement inappropri lors dune de ses permanences dans le
									foyer tudiant.<br/><br/>
									Je soussign
								</Typography>
								<TextField
										required
										onChange={this.handleChange}
										id='name'
										placeholder='Prnom / Nom'
										name="nom"
										className={classes.textField}
										value={newSignature.nom}
								/>
								<Typography variant="body1" className={classes.text}>
									membre de lassociation / membre du groupe, <strong>{currentCreneau.perm.nom}</strong> engage ma
									personne et le reste de mon quipe  respecter les rgles suivantes lors de toutes les permanences
									tenues au Picasso durant le semestre. Respecter les dcisions de lquipe dastreinte. Notamment en ce qui
									concerne les horaires de fermeture, le volume sonore et lutilisation de la licence de cercle priv. Respecter le
									matriel mis  disposition des permanenciers. Respecter les tudiants prsents dans lenceinte du foyer. Vrifier
						 			pass vaccinal de toutes les personnes entrant dans lenceinte du Picasso pendant les heures de permanence.
									Payer mes consommations pendant mes permanences. En effet, les permanences sont tenues bnvolement.
									Leur but est dassurer un service aux tudiants et non de permettre  ceux qui les tiennent den tirer des avantages.
									Ne pas servir une personne qui a trop bu et prvenir lastreinteur si ltat de la personne ncessite
									lintervention de personnel qualifi. Distribuer des thylotests aux conducteurs le demandant. Ceux-ci
									seront  disposition derrire le bar durant les heures de services dalcool (18h30  21h30).
									Rester sobre car nous vous rappelons que vous tes responsable du bon droulement de la permanence.
									Effectuer les tches mnagres de manire correcte en suivant les consignes des astreinteurs.
									La caution est un chque de 200  lordre du BDE UTC Picasso par groupe de permanence, que ce soit
									groupe damis ou association. Le non-respect dune des rgles cites ci-dessus pourra entraner lencaissement
									de la caution, totale ou partielle selon la gravit du problme.<br/><br/>
								</Typography>

								{this.displayDate()}
								<TextField
									required
									onChange={this.handleChange}
									id='login'
									placeholder="Login"
									name="login"
									className={classes.textField}
									value={newSignature.login}
								/>
								{saving? (
									<CircularProgress className={classes.progress} />
								):(
									<Button variant="contained" color="primary" onClick={this.saveSignature}>
										Valider
									</Button>
								)}

							</React.Fragment>
						):(
							<Typography variant="body1">
								Pas de perm en cours ...
							</Typography>
						)}

					</div>
				)}
			<SnackbarComponent
				open={snackbar.open}
				variant={snackbar.variant}
				message={snackbar.message}
				closeSnackbar={
					()=>{
						this.setState({
							snackbar: {
								...this.state.snackbar,
								open: false,
							}
						})
					}
				}
			/>

			</React.Fragment>
		)
  	}
}

const styles = theme => ({
	container: {
		textAlign: 'justify',
		padding: 20,
		margin: 30,
		border: "1.5px solid var(--color-primary)",
	},
	title : {
		textAlign: 'center',
		textDecoration : 'underline var(--color-primary)',
		marginBottom: 30,
	},
	textField: {
		paddingLeft: 10,
		paddingRight: 10,
		color: 'red',
		display: 'inline-block',
		verticalAlign: 'text-bottom',
	},
	text : {
		display: 'inline',
	},
	loader: {
        marginTop: 200,
    },
})

Charte.propTypes = {
 	classes: PropTypes.object.isRequired
}

export default withStyles (styles) (Charte)
